export default class Layout {
  constructor(nDivH, nDivV, maxSize = 5) {
    this.nDivH = nDivH
    this.nDivV = nDivV
    this.map = []
    this.maxSize = maxSize
    this.space = this.nDiv
    this.position = 0
  }

  next() {
    let i, j
    const len = this.position + this.nDivH * this.maxSize

    //左上から空いてる位置を探す
    for (i = this.position; i < len; i++) {
      if (this.map.length === i || !this.map[i]) {
        this.position = i
        break
      }
    }

    let pV = ~~(this.position / this.nDivH)
    if (pV >= this.nDivV) return undefined

    const right = Math.ceil((this.position + 1) / this.nDivH) * this.nDivH,
      bottom = this.nDivV - pV

    let space = 0
    for (i = this.position; i < right; i++) {
      if (this.map[i]) {
        break
      }
      else {
        space++
      }
    }

    const size = ~~(Math.min(space, this.maxSize, bottom) * Math.random()) + 1

    let p = this.position
    for (j = 0; j < size; j++) {
      for (i = 0; i < size; i++) {
        this.map[p++] = 1
      }
      p += this.nDivH - size
    }

    p = this.position
    this.position += size

    return {
      x: p % this.nDivH,
      y: ~~(p / this.nDivH),
      size
    }
  }
}