<template>
  <div></div>
</template>


<script>
import Layout from "./Layout";

export default {
  props: {
    images: {
      type: Array,
      required: true
    },

    nDivH: {
      type: Number,
      required: false,
      defalut: 10
    },

    nDivV: {
      type: Number,
      required: false,
      defalut: 10
    }
  },

  mounted() {
    console.log(this.nDivH);
    this.layout = new Layout(this.nDivH, this.nDivV);

    const originals = this.images.map(url => {
      const img = new Image();
      img.src = url;
      return img;
    });
    console.log(originals);

    const diff = 40;

    // for (let i = 0; i < 20; i++) {
    let next;
    while ((next = this.layout.next())) {
      let image = originals[0].cloneNode(true);

      let style = image.style;
      style.position = "absolute";
      style.width = next.size * diff + "px";
      style.transform =
        "translate(" + next.x * diff + "px, " + next.y * diff + "px)";

      this.$el.appendChild(image);
      // console.log(next);
    }
  }
};
</script>


